.slide-in-right {
  -webkit-animation: slide-in-right 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  -moz-animation: slide-in-right 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  -ms-animation: slide-in-right 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  -o-animation: slide-in-right 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-right 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.slide-in-left {
  -webkit-animation: slide-in-left 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  -moz-animation: slide-in-left 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  -ms-animation: slide-in-left 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  -o-animation: slide-in-left 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-in-left 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.fade-in {
  -webkit-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -moz-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -ms-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -o-animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: fade-in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

.fade-out {
  -webkit-animation: fade-out 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -moz-animation: fade-out 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -ms-animation: fade-out 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  -o-animation: fade-out 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: fade-out 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-30px);
  }
  60% {
    transform: translateX(-15px);
  }
}
@-webkit-keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    -webkit-transform: translateX(0);
  }
  40% {
    -webkit-transform: translateX(-30px);
  }
  60% {
    -webkit-transform: translateX(-15px);
  }
}
@-webkit-keyframes slide-in-right {
  0% {
    @include transform('translateX(270px)');
    opacity: 0;
  }
  100% {
    @include transform('translateX(0)');
    opacity: 1;
  }
}
@keyframes slide-in-right {
  0% {
    @include transform('translateX(270px)');
    opacity: 0;
  }
  100% {
    @include transform('translateX(0)');
    opacity: 1;
  }
}
@-webkit-keyframes slide-in-left {
  0% {
    @include transform('translateX(-20px)');
    opacity: 0;
  }
  100% {
    @include transform('translateX(0px)');
    opacity: 1;
  }
}
@keyframes slide-in-left {
  0% {
    @include transform('translateX(-200px)');
    opacity: 0;
  }
  100% {
    @include transform('translateX(-0px)');
    opacity: 1;
  }
}
@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
